import React,{Component} from "react";
import "./index.scss"

import { WingBlank, WhiteSpace } from 'antd-mobile';
import img from "../../../assets/images/images/123123213.jpg"
import img2 from "../../../assets/images/images/12312312.jpg"
import { history } from "../../../utils";

var PersonalhomepageObj = [
    {
        url:"",
        name:"",
        age:1,
        path:"/listenMusic"
    },
    {
        url:"",
        name:"",
        age:1,
        ages: 0 ,
        path:"/songsheet"
    },
]


export default class Personalhomepage extends React.Component{
    render(){
        return(
            <WingBlank>
                <div className="Personalhomepage">
                    <ul>
                        <li onClick={()=>history.push("/listenMusic")}>
                            <img src={img} alt=""/>
                            <div>
                                <span></span>
                                <span>
                                     0 
                                </span>
                            </div>
                        </li>
                        <li>
                            <img src={img2} alt=""onClick={()=>history.push("/songsheet")}/>
                            <div>
                                <span></span>
                                <span>
                                    0, 0
                                </span>
                            </div>
                        </li>
                        <li>
                            <div>
                                <span> ()</span>
                            </div>
                            <div>
                                : 1 (201911)
                            </div>
                        </li>
                    </ul>
                </div>
            </WingBlank>
        )
    }
}